import React, {useState} from 'react';
import cn from "classnames";
import styles from './Paginator.module.css';


let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 20}) => {

    // Функция Math.ceil() округляет число в большую сторону.
    // Далее общее количество пользователей делим на количество пользователей на одной странице.
    // Далее в переменной pagesCount получаем общее количество кнопок на нашем пагинаторе. 
    let pagesCount = Math.ceil(totalItemsCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    };

    // Общее количество кнопок на нашем пагинаторе(pagesCount) делим на количество показываемых кнопок на странице(portionSize).
    // Далее количество порций страниц по 10 пользователей на страницу, например если общее количество пользователей
    // в приложении будет равна 190, а показатель количества пользователей на страничку равна 10,
    // то количество порций будет составлять 19, и мы получаем результат количества порций страниц(portionCount)
    let portionCount = Math.ceil(pagesCount / portionSize);     // 19

    let [portionNumber, setPortionNumber] = useState(1);

    // Тут мы определяем левую границу порций, изначально по стандарту у нас идёт первая порция
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    
    // Тут мы определяем правую границу порций
    let rightPortionPageNumber = portionNumber * portionSize;

    // return <div className={styles.containerPaginator}>
    //         {pages.map(p => {
    //             return (
    //                 <span className={`${currentPage === p && styles.selectedPage} ${styles.pageItem}`} 
    //                     onClick={(e) => {onPageChanged(p)}}>
    //                     {p}
    //                 </span>
    //             )
    //         })}
    //     </div>

    return (
        <div className={styles.paginator}>
            {portionNumber > 1 && <button onClick={ () => {setPortionNumber(portionNumber - 1)} }>PREV</button>}
                {
                    pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
                         .map((p) => {return <span className={cn({[styles.selectedPage]: currentPage === p}, styles.pageNumber)}
                                                   key={p}
                                                   onClick={(e) => {onPageChanged(p)}}>
                                                {p}
                                             </span>
                    })
                }
            {portionCount > portionNumber && <button onClick={ () => {setPortionNumber(portionNumber + 1)} }>NEXT</button>}
        </div>
    )
}


export default Paginator;
